        "note": "His was the seemingly effortless styleachieved, I knew, with great effortthat I wanted to emulate, and whenever I began a new project I would first read some White to get his cadences into my ear. \n\n: William Zinsser. On Writing Well, 30th Anniversary Edition: An Informal Guide to Writing Nonfiction Apple Books. \n\nThe lesson from groups such as the Hadza, Tsimane and others is that volume matters more than intensity. They are on their feet and moving from sunrise until dusk, racking up more than two hours of physical activity a day, most of it as walking. We can emulate these same habits by walking or biking instead of driving, taking the stairs, and finding ways to work and play that keep us off our butts.  \n      \nEvolved to exercise -- Scientific American 2019-01"
