        "note": "Poverty premium\n\n\n\nThe volatility of prices makes it difficult to detect a reward for speculation, or risk premium, in any single commodity market\n\nrisk premium \n\n: calibre. The Economist [Fri, 05 Apr 2019] Apple Books. \n\nSolid numbers are in short supply, but Munich Re, a reinsurer, reckons that a market that wrote $4bn of premiums in 2018 could be writing $8bn-9bn by 2020. \n\n: calibre. The Economist [, 25 1 2019] Apple Books. \n\nHunting and gathering also put an evolutionary premium() on intelligence. Technological innovation and creativity meant more calories and better odds of reproduction(). Social intelligence would have proved invaluable, as coordination and communication became ingrained() parts of the hominin strategy.  \n      \nEvolved to exercise -- Scientific American 2019-01\n\nDiscoveries by Alison Brooks of George Washington University, Rick Potts of the Smithsonian National Museum of Natural History and their colleagues at the site of Olorgesailie Basin in Kenya, published in 2018, show that by 320,000 years ago, hominin cognition had blossomed into the kind of sophistication seen in modern humans, with black and red pigments for visual expression and long-distance trade networks for premium() stone tool material. The age of these finds corresponds well with the oldest Homo sapiens fossils found to date, reported in 2017 from the 300,000-year-old site of Jebel Irhoud in Morocco.  \n      \nEvolved to exercise -- Scientific American 2019-01"
