        "note": "deep-seated trait \n\nHaving learned, in the 1950s, that genes were written on long molecules of DNA like stock prices on tickertape, by the 1970s scientists were able to start to move traits from organisms in which they evolved to organisms in which they could be useful by cutting and pasting pieces of that tickertape.\n\n: calibre. The Economist [Fri, 05 Apr 2019] Apple Books. \n\nNo trait evolves in isolation: brains must fit snugly inside their skulls, teeth inside their jaws; muscles, nerves and bones must function harmoniously. Behavioral traits are no different. When a behavioral strategysuch as hunting and gatheringbecomes the norm, physiology adapts to accommodate and even depend on it.  \n      \nEvolved to exercise -- Scientific American 2019-01"
